import { getVideoGroupList, getVideoCategoryList, getVideoTimeline, getVideoGroupId, getVideoRecommend } from '@/request/api/video.js';
export default {
  data() {
    return {
      // 分类
      cat: '全部视频',
      // 视频标签列表信息
      groupList: [],
      // 热门视频标签
      hotLists: [],
      // 是否显示分类选项卡片
      isTagsBox: false,
      // 当前标签 id
      nowTagId: 0,
      // 选中标签(-1为默认：全部视频)
      tagActive: -1,
      // 渲染的视频
      videos: []
    };
  },

  mounted() {
    // 获取视频标签列表
    this.getVideoGroupList(); // 组件创建时添加分类选项卡片监听器

    window.addEventListener('click', this.otherClose);

    if (sessionStorage.getItem("cookie")) {
      // 获取视频分类列表(需登录)
      this.getVideoCategoryList(); // 获取全部视频列表(需登录)

      this.getVideoTimeline();
    }
  },

  beforeDestroy() {
    /* 组件销毁的时候要移除侦听器 */
    window.removeEventListener('click', this.otherClose);
  },

  methods: {
    // 获取视频标签列表 
    async getVideoGroupList() {
      let res = await getVideoGroupList();
      this.groupList = res.data.data; // console.log('视频标签列表', this.groupList);
    },

    // 获取视频分类列表 
    async getVideoCategoryList() {
      let res = await getVideoCategoryList(sessionStorage.getItem("cookie"));
      this.hotLists = res.data.data; // console.log('热门视频分类列表', this.hotLists);
    },

    // 获取全部视频列表 (可获取视频分类列表)(需登录) 只返回 8个？
    async getVideoTimeline() {
      let res = await getVideoTimeline(sessionStorage.getItem("cookie"), 30); // console.log('获取全部视频列表', res);

      this.videos = res.data.datas; // 保留类型为 1的，箭头函数用 filter() 方法过滤，返回的 item 是一个个对象

      this.videos = this.videos.filter(item => item.type == 1); // console.log('全部视频列表', this.videos);
    },

    // 获取标签/分类下的视频 (传入id) 只返回 8个？
    async getVideoGroupId(id) {
      let res = await getVideoGroupId(id, 30, sessionStorage.getItem("cookie")); // console.log('获取标签id' + id + '下的视频', res);

      this.videos = res.data.datas; // console.log('标签id' + id + '下的视频', this.videos);
    },

    // 获取推荐视频
    async getVideoRecommend() {
      const res = await getVideoRecommend(sessionStorage.getItem("cookie"), 10); // console.log('获取推荐视频', res);
    },

    // 点击右上角热门分类更换歌单列表
    change(index, id, name) {
      if (this.nowTagId !== id) {
        this.cat = name;
        this.nowTagId = id; // 右上角样式排他

        this.exclusiveHot(index); // 卡片样式排他 使用 findIndex() 查找

        const activeIndex = this.groupList.findIndex(item => item.name === name);
        this.tagActive = activeIndex; // 获取标签/分类下的视频

        if (sessionStorage.getItem("cookie")) this.getVideoGroupId(this.nowTagId);
      }
    },

    // 点击分类选项卡片更换视频
    tagsChange(id, index, name) {
      this.cat = name;
      this.nowTagId = id;
      this.tagActive = index;
      this.isTagsBox = false; // 右上角先全部置为默认样式
      // for (var i = 0; i < this.$refs.hotTags.children.length; i++) {
      //   this.$refs.hotTag[i].style.backgroundColor = '#fff'
      //   this.$refs.hotTag[i].style.color = 'rgb(94, 94, 94)'
      // }
      // 右上角先全部置为默认样式

      this.$refs.hotTag.forEach(item => {
        item.style.backgroundColor = '#fff';
        item.style.color = 'rgb(94, 94, 94)';
      }); // 点击"全部视频"

      if (id == 0) {
        // 直接获取全部视频列表
        this.getVideoTimeline(); // 后面的操作不执行

        return;
      } else if (sessionStorage.getItem("cookie")) {
        // 获取标签/分类下的视频
        this.getVideoGroupId(this.nowTagId);
      } // 如果属于右上角的热门分类，则也显示样式，若没找到 activeIndex 返回 -1


      const activeIndex = this.hotLists.findIndex(item => item.name === name);
      if (activeIndex != -1) this.exclusiveHot(activeIndex);
    },

    // 显示与隐藏分类选项卡片
    changeIsTagsBox() {
      this.isTagsBox = !this.isTagsBox;
    },

    // 点击其它地方，分类选项卡片关闭
    otherClose(e) {
      // 需要打开了分类选项卡片才能使用 tags_box.contains() 方法，所以要加上 this.isTagsBox  == true 条件
      if (this.isTagsBox == true && this.$refs.tags_box.contains(e.target)) {// console.log('在分类选项卡片中');
      }

      if (this.isTagsBox == true && !this.$refs.tags_box.contains(e.target) && !this.$refs.options_left.contains(e.target)) {
        // console.log('正在点击其它地方!关闭分类选项卡片');
        this.isTagsBox = false;
      }
    },

    // 热门歌单分类样式排他(这里故意保留"排他"的解决方法)
    exclusiveHot(index) {
      for (var i = 0; i < this.$refs.hotTags.children.length; i++) {
        this.$refs.hotTag[i].style.backgroundColor = '#fff';
        this.$refs.hotTag[i].style.color = 'rgb(94, 94, 94)';
      }

      this.$refs.hotTag[index].style.backgroundColor = '#fce9e9';
      this.$refs.hotTag[index].style.color = 'rgb(231, 25, 25)';
    },

    // 鼠标放上 hotTag 文字变黑
    hotTagMouseover(index) {
      if (this.$refs.hotTag[index].style.color != 'rgb(231, 25, 25)') this.$refs.hotTag[index].style.color = '#000';
    },

    // 鼠标移开 hotTag 字色恢复
    hotTagMouseleave(index) {
      if (this.$refs.hotTag[index].style.color != 'rgb(231, 25, 25)') this.$refs.hotTag[index].style.color = 'rgb(94, 94, 94)';
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 去往个人主页
    gotoPersonalHome(userId) {
      this.$router.push({
        path: '/PersonalHome',
        query: {
          userId: userId
        }
      });
    },

    // 去往视频播放页
    goVideoPlayer(videoid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          videoid: videoid,
          // 1是 Mv，2是视频
          type: 2
        }
      });
    }

  }
};