// 导入 axios 实例
import service from "..";

// 需要用户登录后获取携带cookie
// 获取视频标签列表 
export function getVideoGroupList() {
  return service({
    method: 'GET',
    url: '/video/group/list'
  })
}

// 获取视频分类列表(需登录)
export function getVideoCategoryList(cookie) {
  return service({
    method: 'GET',
    url: `/video/category/list?cookie=${cookie}`
  })
}

// 获取视频标签/分类下的视频 (传入标签/分类id,可获取到相关的视频,分页参数只能传入 offset)
export function getVideoGroupId(id, limit, cookie) {
  return service({
    method: 'GET',
    url: `/video/group?id=${id}&limit=${limit}&cookie=${cookie}`
  })
}

// 获取全部视频列表 (可获取视频分类列表)(需登录)
export function getVideoTimeline(cookie, limit) {
  return service({
    method: 'GET',
    url: `/video/timeline/all?limit=${limit}&cookie=${cookie}`
  })
}

// 获取推荐视频(需登录)
export function getVideoRecommend(cookie, offset) {
  return service({
    method: 'GET',
    url: `/video/timeline/recommend?offset=${offset}&cookie=${cookie}`
  })
}

// 获取最新 mv(第二个参数不填则为全部)
export function getLatestMv(limit, area) {
  return service({
    method: 'GET',
    url: `/mv/first?limit=${limit}&area=${area}`
  })
}

// 获取热播 mv
export function getHotMv() {
  return service({
    method: 'GET',
    url: '/mv/all?order=最热&limit=8'
  })
}

// 获取网易出品 mv
export function getNetEaseProduceMv() {
  return service({
    method: 'GET',
    url: '/mv/all?type=网易出品&limit=8'
  })
}

// 获取 mv 排行
export function getTopMv(limit, area) {
  return service({
    method: 'GET',
    url: `/top/mv?limit=${limit}&area=${area}`
  })
}

// 获取全部 mv
export function getAllMv(area, type, order, limit) {
  return service({
    method: 'GET',
    url: `/mv/all?area=${area}&type=${type}&order=${order}&limit=${limit}`
  })
}